* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html {
  min-height: 1vh; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
  min-height: 1vh; }

/* COLORS */
/* PADDINGS AND MARGINS */
/* MEDIA QUERIES */
@media (min-width: 768px) {
  html {
    font-size: 46.875%; } }

@media (min-width: 1024px) {
  html {
    font-size: 62.5%; } }

@media (min-width: 204.8px) {
  .content-holder {
    width: calc(100% - (.2 * 368px)); } }

@media (min-width: 768px) {
  .content-holder {
    width: calc(100% - (.75 * 352px)); } }

@media (min-width: 1024px) {
  .content-holder {
    width: calc(100% - (1 * 352px)); } }

.content-holder {
  background-color: #eef0f3;
  display: inline-block;
  min-height: 100vh; }

.navbar {
  margin: 0;
  padding: 0;
  background-color: #ffffff; }

.content .navbar h2 {
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
  color: #a0a1a5;
  margin: 0;
  text-align: left; }



