* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html {
  min-height: 1vh; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
  min-height: 1vh; }

/* COLORS */
/* PADDINGS AND MARGINS */
/* MEDIA QUERIES */
@media (min-width: 768px) {
  html {
    font-size: 46.875%; } }

@media (min-width: 1024px) {
  html {
    font-size: 62.5%; } }

.company {
  background-color: #ffffff;
  height: 96px;
  border-radius: 2px;
  margin: 10px 24px; }

.company-logo {
  width: 48px;
  height: 48px;
  background-color: #e7e8ec;
  margin: 24px auto 24px 24px;
  display: inline-block;
  float: left; }

.company-name {
  font-size: 20px;
  font-weight: 600;
  color: #25272a;
  display: inline-block;
  float: left;
  vertical-align: middle;
  line-height: 96px;
  text-align: left; }

.company-data {
  display: inline-block;
  vertical-align: middle;
  text-align: left;
  float: left; }

.company-data-point {
  color: #a0a1a5;
  font-weight: bold;
  font-size: 12px;
  margin-top: 30px;
  margin-bottom: 6px; }

.company-data-value {
  color: #25272a;
  font-size: 16px; }

.company-arrow {
  display: inline-block;
  vertical-align: top;
  line-height: 96px;
  text-align: right; }

.company-arrow img {
  vertical-align: middle; }



