.browseComps {
  margin: 0px;
  background-color: white; }

.browseCompsHolder {
  color: #354052;
  font-size: 16px; }

.line {
  height: 2px;
  border: solid 1px #ccd3da; }

.findCompanies {
  height: 96px;
  background-color: white;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.33;
  text-align: left;
  color: #354052; }

.findCompaniesText {
  margin: 24px 12px 12px 12px; }

.searchIcon {
  width: 36px;
  height: 36px;
  border-radius: 4px;
  background-color: peachpuff;
  margin: 24px 12px 12px 12px; }



